.mobile-header > .logo {
  color: $full-white;
}

.menu-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;

  min-width: 100vw;
  min-height: 100vh;
  padding-left: 20px;
  padding-right: 20px;
  // background-color: rgba(17, 21, 24, 0.9);
  background-image: linear-gradient(to right, rgba(17, 21, 24, 0.9), rgba(17, 21, 24, 0.9));

  transform: translateX(100%);
  transition: transform 250ms ease-in-out;

  @include for-size(min-tablet) {
    padding-left: 40px;
    padding-right: 40px;
  }
}

.menu-container.is-open {
  transform: translateX(0);
}

.mobile-header {
  justify-content: space-between;
  display: flex;
  padding-top: 30px;

  @include for-size(min-tablet) {
    margin-bottom: 72px;
    padding-top: 42px;
  }
}

.mobile-list {
  margin-top: 50px;
  margin-bottom: 50px;

  @include for-size(min-tablet) {
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 178px;
  }
}

.mobile-list__item:not(:last-child) {
  margin-bottom: 20px;
}

.mobile-list__link {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 180%;
  letter-spacing: 0.03em;
  color: $full-white;

  @include for-size(min-tablet) {
    font-size: 26px;
  }
}

.mobile-list__link:hover,
.mobile-list__link:focus {
  color: $main-orange;
}

.menu-container > .call {
  margin-left: 10px;
  display: flex;

  @include for-size(min-tablet) {
    display: none;
  }
}

